












import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class AVTextInput extends Vue {
  @Prop({ default: "Enter your text..." })
  text!: string;
  @Prop()
  btnClick!: Function;

  onButtonClickHandler(): void {
    this.$emit("btn-click", this.btnClick("CLICKED IT"));
  }
}
